import React, { useState } from "react";

import Footer from "../components/Footer";
import Projects from "../data/projects.json";
import Filters from "../data/filters.json";
import Project from "../components/Project";
import DetailsPopup from "../components/DetailsPopup";

export default function Home() {
  const [displayPopup, setDisplayPopup] = useState(-1);

  const _showPopup = (index: number) => setDisplayPopup(index);

  return (
    <div id="main">
      {Projects.map((project, i) => (
        <DetailsPopup
          key={i}
          index={i}
          details={project}
          display={displayPopup === i}
          hide={() => setDisplayPopup(-1)}
        />
      ))}
      <section className="section site-portfolio">
        <div className="container">
          <div className="row mb-5 align-items-center">
            <div className="col-md-12 col-lg-6 mb-4 mb-lg-0" data-aos="fade-up">
              <h2>Hey, I'm Naeem Qaswar</h2>
              <p className="mb-0">
                Professional Web &amp; Mobile Application Developer
              </p>
            </div>
            <div
              className="col-md-12 col-lg-6 text-start text-lg-end"
              data-aos="fade-up"
              data-aos-delay="100"
            >
              <div id="filters" className="filters">
                {Filters.map(({ name, title }, i) => (
                  <a
                    href="#"
                    data-filter={name == "*" ? name : `.${name}`}
                    className={i == 0 ? "active" : ""}
                  >
                    {title}
                  </a>
                ))}
              </div>
            </div>
          </div>
          <div
            id="portfolio-grid"
            className="row no-gutter"
            data-aos="fade-up"
            data-aos-delay="200"
          >
            {Projects.map((project, i) => (
              <Project
                key={i}
                index={i}
                details={project}
                showPopup={_showPopup}
              />
            ))}
          </div>
        </div>
      </section>
      <Footer />
    </div>
  );
}
